/*属性变量*/
.warning-warp .analysis-time {
  justify-content: center;
  width: 3.5rem;
  display: flex;
  align-items: center;
  margin: 0 auto .1rem;
}
.warning-warp .analysis-time ul {
  flex: 1;
}
.warning-warp .analysis-time ul li {
  padding: 0 0.1rem;
  display: inline-block;
  margin: 0 0.04rem;
  cursor: pointer;
}
.warning-warp .analysis-time ul li.active {
  color: #52D4E0;
  border-radius: 50px;
  border: 1px solid #52D4E0;
}
.warning-warp .analysis-time ul li:last-child {
  margin: 0;
}
.warning-warp .sort-list {
  padding: 0 .07rem;
}
.warning-warp .sort-list .content {
  width: calc(99.64%);
}
.update-range-time {
  text-align: center;
}
.update-range-time i {
  float: right;
  margin-right: .49rem;
}
.number-total {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 6.4rem;
  border-top: 0.01rem solid #5AB0F3;
  border-bottom: 0.01rem solid #5AB0F3;
  padding: 0.25rem 0;
  margin: 0 auto .1rem;
}
.number-total dl {
  flex: 1;
  text-align: center;
  border-right: .01rem solid #ffffff;
}
.number-total dl:last-child {
  border-right-color: transparent;
}
.number-total dl dt {
  font-size: .44rem;
  color: #F3Af1B;
}
